/**************************************/
/* Styles (LESS/CSS) files supervisor */
/**************************************/
/* Third party */
/* 
 *  Owl Carousel - Animate Plugin
 */
.owl-carousel .animated {
  -webkit-animation-duration: 1000ms;
  animation-duration: 1000ms;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.owl-carousel .owl-animated-in {
  z-index: 0;
}
.owl-carousel .owl-animated-out {
  z-index: 1;
}
.owl-carousel .fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}
@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
/* 
 * 	Owl Carousel - Auto Height Plugin
 */
.owl-height {
  -webkit-transition: height 500ms ease-in-out;
  -moz-transition: height 500ms ease-in-out;
  -ms-transition: height 500ms ease-in-out;
  -o-transition: height 500ms ease-in-out;
  transition: height 500ms ease-in-out;
}
/* 
 *  Core Owl Carousel CSS File
 */
.owl-carousel {
  display: none;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  /* position relative and z-index fix webkit rendering fonts issue */
  position: relative;
  z-index: 1;
}
.owl-carousel .owl-stage {
  position: relative;
  -ms-touch-action: pan-Y;
}
.owl-carousel .owl-stage:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}
.owl-carousel .owl-stage-outer {
  position: relative;
  overflow: hidden;
  /* fix for flashing background */
  -webkit-transform: translate3d(0px, 0px, 0px);
}
.owl-carousel .owl-controls .owl-nav .owl-prev,
.owl-carousel .owl-controls .owl-nav .owl-next,
.owl-carousel .owl-controls .owl-dot {
  cursor: pointer;
  cursor: hand;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.owl-carousel.owl-loaded {
  display: block;
}
.owl-carousel.owl-loading {
  opacity: 0;
  display: block;
}
.owl-carousel.owl-hidden {
  opacity: 0;
}
.owl-carousel .owl-refresh .owl-item {
  display: none;
}
.owl-carousel .owl-item {
  position: relative;
  min-height: 1px;
  float: left;
  -webkit-backface-visibility: hidden;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.owl-carousel .owl-item img {
  display: block;
  width: 100%;
  -webkit-transform-style: preserve-3d;
}
.owl-carousel.owl-text-select-on .owl-item {
  -webkit-user-select: auto;
  -moz-user-select: auto;
  -ms-user-select: auto;
  user-select: auto;
}
.owl-carousel .owl-grab {
  cursor: move;
  cursor: -webkit-grab;
  cursor: -o-grab;
  cursor: -ms-grab;
  cursor: grab;
}
.owl-carousel.owl-rtl {
  direction: rtl;
}
.owl-carousel.owl-rtl .owl-item {
  float: right;
}
/* No Js */
.no-js .owl-carousel {
  display: block;
}
/* 
 * 	Owl Carousel - Lazy Load Plugin
 */
.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  -webkit-transition: opacity 400ms ease;
  -moz-transition: opacity 400ms ease;
  -ms-transition: opacity 400ms ease;
  -o-transition: opacity 400ms ease;
  transition: opacity 400ms ease;
}
.owl-carousel .owl-item img {
  transform-style: preserve-3d;
}
/* 
 * 	Owl Carousel - Video Plugin
 */
.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000;
}
.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  background: url("owl.video.play.png") no-repeat;
  cursor: pointer;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  -webkit-transition: scale 100ms ease;
  -moz-transition: scale 100ms ease;
  -ms-transition: scale 100ms ease;
  -o-transition: scale 100ms ease;
  transition: scale 100ms ease;
}
.owl-carousel .owl-video-play-icon:hover {
  -webkit-transition: scale(1.3, 1.3);
  -moz-transition: scale(1.3, 1.3);
  -ms-transition: scale(1.3, 1.3);
  -o-transition: scale(1.3, 1.3);
  transition: scale(1.3, 1.3);
}
.owl-carousel .owl-video-playing .owl-video-tn,
.owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none;
}
.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  -webkit-transition: opacity 400ms ease;
  -moz-transition: opacity 400ms ease;
  -ms-transition: opacity 400ms ease;
  -o-transition: opacity 400ms ease;
  transition: opacity 400ms ease;
}
.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
}
/* Custom */
.svg-sprite--arrow {
  background-image: url("../img/sprites/svg/svg-sprite.svg");
  background-position: -2px -2px;
  width: 36px;
  height: 36px;
  background-repeat: no-repeat;
  background-size: 40px 40px;
}
.no-svg .svg-sprite--arrow {
  background-image: url("../img/sprites/svg/svg-sprite.png");
}
.cg-menu {
  width: 100%;
}
.cg-menu ul {
  list-style-type: none;
  margin: auto;
  padding: 0;
}
.cg-menu ul:after,
.cg-menu ul:before {
  content: '';
  display: block;
  clear: both;
}
.cg-menu li a {
  display: block;
  float: left;
  font-size: 18px;
  width: 12.5%;
  background-color: #00afd4;
  color: white;
  text-decoration: none;
  padding-left: 13px;
  padding-right: 13px;
  padding-top: 20px;
  height: 130px;
  box-sizing: border-box;
  transition: all 500ms;
  line-height: 1.2;
}
.cg-menu li a:hover {
  background-color: #018ba8;
}
.cg-menu li:nth-child(odd) a {
  background-color: #009ebf;
}
.cg-menu li:nth-child(odd) a:hover {
  background-color: #0286a2;
}
.cg-menu li:nth-child(2n+9) a {
  background-color: #00afd4;
}
.cg-menu li:nth-child(2n+9) a:hover {
  background-color: #018ba8;
}
.cg-menu li:nth-child(2n+10) a {
  background-color: #009ebf;
}
.cg-menu li:nth-child(2n+10) a:hover {
  background-color: #0286a2;
}
.cg-comments {
  width: 100%;
  text-align: center;
}
.cg-comments__container {
  width: 100%;
  max-width: 460px;
  margin: auto;
}
.cg-comments__title {
  font-size: 24px;
  color: #343434;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 40px;
}
.cg-comments .owl-next {
  position: absolute;
  top: 50px;
  right: 0;
}
.cg-comments .owl-next div {
  transform: rotate(-90deg);
}
.cg-comments .owl-prev {
  position: absolute;
  top: 50px;
  left: 0;
}
.cg-comments .owl-prev div {
  transform: rotate(90deg);
}
.cg-comment__img {
  width: 135px;
  height: 135px;
  background-position: 50% 50%;
  background-size: cover;
  border-radius: 50%;
  margin: auto;
  margin-bottom: 20px;
}
.cg-comment__name {
  font-weight: 700;
  color: #343434;
  margin-bottom: 55px;
}
.cg-comment__comment {
  text-align: left;
  color: #616161;
}
.cg-comment p {
  margin-bottom: 25px;
  margin-top: 0;
}
.cg-articles {
  text-align: center;
}
.cg-articles__title {
  font-size: 24px;
  color: #343434;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 45px;
}
.cg-articles__grid {
  display: -webkit-inline-flex;
  display: -moz-inline-flex;
  display: -ms-inline-flex;
  display: -o-inline-flex;
  display: inline-flex;
  flex-wrap: wrap;
}
.cg-articles__grid > div {
  width: 50%;
  padding-right: 10px;
  box-sizing: border-box;
  margin-bottom: 50px;
}
.cg-articles__grid > div:nth-child(even) {
  padding-right: 0;
  padding-left: 10px;
}
.cg-articles .cg-btn {
  margin: auto;
}
.cg-article {
  text-align: left;
}
.cg-article__title {
  font-size: 24px;
  font-weight: 700;
  color: #00afd4;
  margin-bottom: 30px;
}
.cg-article__image {
  margin-bottom: 34px;
}
.cg-article img {
  width: 100%;
}
.cg-btn {
  appearance: none;
  box-shadow: none;
  outline: none;
  background-color: #00afd4;
  color: white;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  border: 0;
  line-height: 1;
  padding: 15px 25px;
  cursor: pointer;
}
.cg-btn:active,
.cg-btn:focus {
  box-shadow: none;
  outline: none;
}
.cg-btn:hover {
  background-color: #009ebf;
  text-decoration: none;
}
a:hover > .cg-btn {
  text-decoration: none;
}
.cg-simple-page img {
  width: 100%;
}
.cg-simple-page .cg-btn {
  position: absolute;
  margin-top: 23px;
}
.cg-simple-page__title {
  font-size: 24px;
  font-weight: 700;
  color: #343434;
  margin-top: 30px;
  margin-bottom: 50px;
  text-align: center;
  text-transform: uppercase;
}
.cg-simple-page__content {
  width: 100%;
  margin: auto;
  max-width: 460px;
  color: #343434;
}
.cg-simple-page--article .cg-simple-page__title {
  color: #00afd4;
}
.cg-person-cards__title {
  margin: 70px 0 90px;
  font-size: 24px;
  font-weight: 700;
  color: #343434;
  text-transform: uppercase;
  text-align: center;
}
.cg-person-cards__grid {
  display: -webkit-inline-flex;
  display: -moz-inline-flex;
  display: -ms-inline-flex;
  display: -o-inline-flex;
  display: inline-flex;
  flex-wrap: wrap;
  width: 100%;
}
.cg-person-cards__grid > div {
  width: 25%;
  box-sizing: border-box;
}
.cg-person-card {
  text-align: center;
  padding: 0 10px;
  color: #616161;
}
.cg-person-card__image {
  width: 135px;
  height: 135px;
  margin: auto;
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 50%;
  margin-bottom: 10px;
}
.cg-person-card__name {
  color: #00afd4;
  margin-bottom: 10px;
}
.cg-person-card__position {
  margin-bottom: 25px;
}
